

.laman {
  background-color: #003333;
  padding: 15px;
  width: 256px;
  display: flex;
  color: #f4f5f4;
  font-size: 16px;
  gap: 15px;
}
.laman p{
  margin: 0;
}


.lenta {
  display: flex;
  flex-direction: column;
  width: 474px;
  border-top-left-radius: 35px;
  /*border: 1px solid #003333;*/
  background-color: #f4f5f4;
  margin: 25px;
  box-shadow: 4px 4px 4px 0px #00333340;
  
}

.main {
  
  margin-right:5%;
  margin-left: 10%;
}

.gugu {
  display: flex;
  flex-wrap: wrap;
}

.nav {
  width: 15%;
  display: flex;
  flex-direction: column;
  background-color: #171717;
  right: 0%;
  position: absolute;
  align-items: center;
  height: 100vh;
}

.nav li {
  width: 100%;
  display: flex;
  align-items: center;
}

.span {
  display: flex;
  align-items: center;
  width: fit-content;
  justify-content: space-around;
}

.bg {
  position: static;
}

.container {
  position: relative;
  width: 100%;
  height: 100vh;
}

.imageWrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 95%;
  height: 65%;
  z-index: -1;
}

.bg {
  object-fit: cover;
}

.span button {
  width: 25px;
  height: 25px;
  border: 0;
  border-radius: 12px;
}

.span p {
  margin: 5px;
}

.ing {
  display: flex;
  flex-direction: column;
  font-size: larger;
  width: fit-content;
  background-color: #ffffff33;
  padding: 20px;
}

.ing ul {
  margin-left: 0;
  padding-left: 0;
  list-style-position: outside;
}

.box {
  display: flex;
  flex-direction: row;
  font-size: 1em;
}

.Link {
  position: relative;
  width: 100%;
  height: 50%;
}

.Image {
  border-top-left-radius: 35px;
  border-bottom-right-radius: 35px;
  width: 100%;
  height: auto;
}



.tags{
  display: flex;
  flex-direction: row;
  margin-bottom: 12px;
  margin-left: 25px;
  gap: 10px;
}
.tags button{
  border: 0;
  border-radius: 50px ;
  padding: 5px;
  background-color: #ffffff;
  color: #003333;
  min-width: 50px;
}
.title{
  font-size: 25px;
}
.infop{
  font-weight: 400;
}

.cards{
  margin: 25px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  height: 100%;
}

.cards a{
  padding: 0;
}


.lama{
  width: 100%;
  height: auto;
}